<template>
  <div id="cesiumComp"></div>
</template>

<script>
export default {
  name: "cesiumComp",
  components: {},
  props: {},
  computed: {},
  watch: {},
  data() {
    return {
      watchBridgeCode: null,
      viewer: null,
      Cesium: null,
      szlfyArr: [], // 裂缝
      wsdyArr: [], // 温湿度
      szcdArr: [
        {
          surveyType: "szcd",
          latitude: "28.416212337871073",
          longitude: "116.35211058849842",
          deviceCode: "10000",
          deviceName: "Rambutan pi",
          deviceState: 0,
          elevation: 30.0,
          alertNum: 0,
        },
        {
          surveyType: "szcd",
          latitude: "28.41512780799344",
          longitude: "116.3521143506911",
          deviceCode: "8705",
          deviceName: "矿场挖掘机",
          deviceState: 0,
          elevation: 20.0,
          alertNum: 0,
        },
        {
          surveyType: "szcd",
          latitude: "28.41512780799344",
          longitude: "116.3521143506911",
          deviceCode: "9000",
          deviceName: "iRaspberry",
          deviceState: 0,
          elevation: 20.0,
          alertNum: 0,
        },
      ], // 应力应变
      ewwyArr: [], // 表面位移
      szczyArr: [], // 动态位移
      monitorArr: [], // 视频监控
    };
  },
  methods: {
    /**
     * @Event 方法
     * @description: 初始化模型
     * */
    initNewModel() {
      const Cesium = window.Cesium;
      this.Cesium = Cesium;
      Cesium.Ion.defaultAccessToken =
        "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJqdGkiOiI5NzgzOTIzYi0xZGM2LTQ0YjAtOTQ5OC1iMzgwMzE0NDU1NjEiLCJpZCI6NjQxNjEsImlhdCI6MTYzMTkyOTQxN30.ujwg4v68wp_-h9k1NNpAc4UMK9uWQPd0iKQ39m-5kKA";
      var viewer = new Cesium.Viewer("cesiumComp", {
        infoBox: false,
        selectionIndicator: false,
        shadows: true,
        shouldAnimate: true,

        // maximumZoomDistance: 100, // 设置相机能够缩放的最大距离
        // minimumZoomDistance: 10  // 设置相机能够缩放的最小距离
      });
      //viewer.terrainProvider=Cesium.createWorldTerrain();

      this.viewer = viewer;
      viewer.entities.removeAll();
      /* 第一个模型的数据 */
      var position = Cesium.Cartesian3.fromDegrees(116.350294, 28.417213, 0.0);
      var heading = Cesium.Math.toRadians(90); // 将度数转换为弧度。
      var pitch = 0;
      var roll = 0;
      var hpr = new Cesium.HeadingPitchRoll(heading, pitch, roll);
      var orientation = Cesium.Transforms.headingPitchRollQuaternion(
        position,
        hpr
      );
      /* 第一个模型的数据 */
      this.addModel(viewer, {
        id: 1,
        name: "model3d/Cesium_Air.glb",
        position: position,
        orientation: orientation,
        model: {
          uri: "models/bridge.glb",
          minimumPixelSize: 50,
          maximumScale: 2000,
        },
      }); // 添加第一个模型

      const handler = new Cesium.ScreenSpaceEventHandler(viewer.scene.canvas);

      handler.setInputAction((movement) => {
        const pick = viewer.scene.pick(movement.position);
        // Cesium.defined(pick) ===> 判断是否在模型上点击； pick.id.id ===> 创建模型时，自定义的id
        if (Cesium.defined(pick)) {
          // console.log(`你点击了模型${pick.id.id}`, pick)
          if (pick.id.id === 1) {
            let entity = viewer.entities.getById(pick.id.id);
            // 存储上一次高亮的模型
            let lastHighlightedEntity = viewer.entities._lastHighlighted;
            // 取消上一次高亮的模型的高亮状态
            if (
              lastHighlightedEntity !== undefined &&
              lastHighlightedEntity !== entity
            ) {
              lastHighlightedEntity.model.color = Cesium.Color.WHITE;
            }
            // 高亮当前模型
            // entity.model.color = Cesium.Color.YELLOW; // 设置整个模型颜色
            entity.model.silhouetteColor = new Cesium.Color(0.5, 0.9, 1.0, 1.0); // 设置边框颜色
            entity.model.silhouetteSize = 2.0; // 设置边框大小
            // entity.position=Cesium.Cartesian3.fromDegrees(121, 30, 100.0); // 使模型移动位置
            //entity.show=false; // 使模型消失
            // 存储当前高亮的模型
            viewer.entities._lastHighlighted = entity;

            viewer.camera.flyTo({
              destination: Cesium.Cartesian3.fromDegrees(
                116.352859,
                28.400755,
                1500.0
              ), // 摄像机在WGS84（世界）坐标中的最终位置或从上向下视图中可见的矩形。（参数：经度，纬度，高度）
              duration: 3, // 飞行持续时间（以秒为单位）。如果省略，Cesium会尝试根据航班飞行的距离来计算理想持续时间。
              orientation: {
                heading: Cesium.Math.toRadians(357.7),
                pitch: Cesium.Math.toRadians(-44.32),
                roll: Cesium.Math.toRadians(0),
              },
            });
          }
        } else {
          /* 在非模型的地方点击 */
          let lastHighlightedEntity = viewer.entities._lastHighlighted;
          if (lastHighlightedEntity !== undefined) {
            // lastHighlightedEntity.model.color = Cesium.Color.WHITE;
            lastHighlightedEntity.model.silhouetteSize = 0;
          }
          // 清空当前高亮的模型
          viewer.entities._lastHighlighted = undefined;
        }
        this.getClickPosition(viewer, movement, Cesium);
      }, Cesium.ScreenSpaceEventType.LEFT_CLICK); // 此枚举类型用于对鼠标事件进行分类:向下、向上、单击、双击、移动和按住按钮时移动。
      viewer.cesiumWidget.creditContainer.style.display = "none";
      viewer.animation.container.style.display = "none"; //隐藏动画控件
      viewer.timeline.container.style.display = "none"; //隐藏时间线控件
      viewer.baseLayerPicker.container.style.display = "none";

      window.cMap = viewer;
    },

    /**
     * @Event 方法
     * @description: 获取点击的坐标
     * */
    getClickPosition(viewer, movement, Cesium) {
      var cartesian = viewer.camera.pickEllipsoid(
        movement.position,
        viewer.scene.globe.ellipsoid
      );
      if (cartesian) {
        var cartographic = Cesium.Cartographic.fromCartesian(cartesian);
        var longitude = Cesium.Math.toDegrees(cartographic.longitude);
        var latitude = Cesium.Math.toDegrees(cartographic.latitude);
        var height = viewer.scene.globe.getHeight(cartographic);
        console.log(
          "经度: " + longitude + "，纬度: " + latitude + "，高度: " + height
        );
      }
    },

    /**
     * @Event 方法
     * @description: 添加模型
     * */
    addModel(viewer, item) {
      viewer.entities.add(item);
    },

    /**
     * @Event 方法
     * @description: 初始化飞行到点位
     * */
    initFlyTo() {
      // 获取目标点位的位置
      const targetPosition = this.Cesium.Cartesian3.fromDegrees(
        116.345859,
        28.408755,
        1000.0
      );

      // 飞行到目标点位
      this.viewer.camera.flyTo({
        destination: targetPosition,
        duration: 5, // 飞行时间
        easingFunction: this.Cesium.EasingFunction.LINEAR_NONE,
        orientation: {
          heading: this.Cesium.Math.toRadians(57.7), // 获取相机的弧度方向
          pitch: this.Cesium.Math.toRadians(-44.32), // 获取相机的弧度（弧度）
          roll: this.Cesium.Math.toRadians(10), // 获取相机弧度角
        },
      });
    },

    /**
     * @Event 方法
     * @description: 根据类型添加标注
     * @param: arr: 存放点位的数组， item: 遍历出来的每一项， imgType: 根据设备类型展示的图片，
     * */
    addMarkerBySurveyType(arr, item, imgType) {
      console.log(arr, item, imgType);
      const position = window.Cesium.Cartesian3.fromDegrees(
        item.longitude,
        item.latitude,
        item.elevation
      );
      const billboard = new window.Cesium.BillboardGraphics({
        image: require(`/public/img/cesiumPoint/${imgType}Green.png`),
        scale: 0.5,
        width: 56,
        height: 175,
      });
      const entity = new window.Cesium.Entity({
        position,
        billboard,
        item,
        deviceCode: item.deviceCode,
      });
      this.viewer.entities.add(entity);
      arr.push(entity);
    },

    /**
     * @Event 方法
     * @description: 绑定点击事件
     * */
    bindMarkerClickEvent(arr) {
      this.viewer.screenSpaceEventHandler.setInputAction((click) => {
        const pickedObject = this.viewer.scene.pick(click.position);
        if (window.Cesium.defined(pickedObject) && pickedObject.id) {
          const deviceCode = pickedObject.id._deviceCode;
          const entity = arr.find((item) => item._deviceCode === deviceCode);
          if (entity) {
            // 执行相应的操作，比如弹出信息窗口等

            // let passData = {
            //     ...entity._item,
            //     surveyName: entity._item.deviceName
            // }
            console.log("点击了点位：", entity);
            this.$message.success("点击了点位" + JSON.stringify(entity._item));
          }
        }
      }, window.Cesium.ScreenSpaceEventType.LEFT_CLICK);
    },

    /**
     * @Event 方法
     * @description: 移除点位
     * @param: arr: 待处理的点位数组
     * */
    removeEntities(arr) {
      if (arr && arr.length > 0) {
        arr.forEach((entity) => {
          this.viewer.entities.remove(entity);
        });
        arr = [];
      }
    },

    /**
     * @Event 方法
     * @description: 获取实时监测页面底部栏按钮传递来的数据
     * */
    getBottomItemData(item) {
      if (item.dictValue !== item.imgUrl) {
        /* 移除对应的点位信息 */
        switch (item.dictValue) {
          case "szlfy":
            this.removeEntities(this.szlfyArr);
            break;
          case "wsdy":
            this.removeEntities(this.wsdyArr);
            break;
          case "szcd":
            this.removeEntities(this.szcdArr);
            break;
          case "ewwy":
            this.removeEntities(this.ewwyArr);
            break;
          case "szczy":
            this.removeEntities(this.szczyArr);
            break;
          case "monitor":
            this.removeEntities(this.monitorArr);
            break;
        }
      } else {
        this.szcdArr.forEach((item) => {
          this.addMarkerBySurveyType(this.szcdArr, item, "szcd");
          this.bindMarkerClickEvent(this.szcdArr);
        });
      }
    },
  },
  created() {},
  mounted() {
    this.initNewModel();
    this.getBottomItemData({ dictValue: "szcd", imgUrl: "szcd" });
    // this.bindMarkerClickEvent();
    this.initFlyTo();
  },
};
</script>

<style lang="scss" scoped>
#cesiumComp {
  height: 100%;
  width: 100%;
}
</style>
